import React, { useReducer, useEffect } from 'react';
import styled from 'styled-components';
import { StepTitle } from '../configurator';
import util from '../util';

const SelectCard = styled.div`
  white-space: pre-wrap;
  padding: 1.5rem;
  margin-bottom: 1rem;

  background: #FFFFFF;
  box-shadow: ${props => props.isSelected ? '0px 0px 30px rgba(96, 254, 178, 1)' : '0px 0px 30px rgba(51, 1, 109, 0.1)'};
  border-radius: 8px;

  &:hover {
    box-shadow: 0px 0px 30px rgba(96, 254, 178, 1);
  }
`;

const SingleMultiSelect = ({ dispatch, dispatchHandle, isSingle, selectOptions, title, hasOptionalInput }) => {
  useEffect(() => {
    return () => {
      dispatch({
        type: 'step_result',
        step_field: dispatchHandle,
        step_result: selectedList
      });
    }
  }, []);

  const [state, innerDispatch] = useReducer(util.reducerSingleMulti, util.initialStateSingleMulti(selectOptions));
  const { selectedList, tvCount } = state;

  return (
    <>
      <StepTitle title={title} />
      <div className=''>
        {selectedList.map(({ option, isSelected }, index) => (
          <SelectCard
            key={index}
            isSelected={isSelected}
            onClick={() => {
              innerDispatch({
                type: isSingle ? 'update_list_item_single' : 'update_list_item',
                index: index,
                dispatchFunc: dispatch,
                dispatchObj: {
                  type: 'step_result',
                  step_field: dispatchHandle,
                }
              });
            }}
          >
            <div className='d-inline-block'>
              <h3><span className={'mr-2 icon checkbox ' + (isSelected && 'active')}></span>{option}</h3>
            </div>
          </SelectCard>
        ))}

        {hasOptionalInput && (
          <SelectCard
            isSelected={tvCount !== ''}
            onClick={() => {
              innerDispatch({ type: 'tv_value_change', value: ' ' })
            }}
          >
            <div className='d-inline-block'>
              <h3>
                <span className={'mr-2 icon checkbox ' + (tvCount !== '' && 'active')}></span>
                <input
                  value={tvCount}
                  className='clear'
                  placeholder="Въведи брой"
                  onChange={(v) => {
                    innerDispatch({ type: 'tv_value_change', value: v.target.value })
                    let newList = [{ option: v.target.value + ' телевизора', isSelected: true }];
                    dispatch({
                      type: 'step_result',
                      step_field: dispatchHandle,
                      step_result: newList,
                    })
                  }}
                />
              </h3>
            </div>
          </SelectCard>
        )}
      </div>
    </>
  )
}

export default SingleMultiSelect;