import constants from '../../constants/configurator'

export default {
  reducerSingleMulti: (state, action) => {
    switch (action.type) {
      case 'update_list_item': {
        const index = action.index;
        let newSelectedList = state.selectedList;
        newSelectedList[index].isSelected = !state.selectedList[index].isSelected;

        action.dispatchObj.step_result = newSelectedList;
        action.dispatchFunc(action.dispatchObj);

        return {
          ...state,
          selectedList: newSelectedList
        }
      }
      case 'update_list_item_single': {
        const index = action.index;
        let newSelectedList = state.selectedList.map(({ option, _ }) => { return { option: option, isSelected: false } });
        newSelectedList[index] = { option: newSelectedList[index].option, isSelected: true };

        action.dispatchObj.step_result = newSelectedList;
        action.dispatchFunc(action.dispatchObj);

        return {
          ...state,
          tvCount: '',
          selectedList: newSelectedList,
        }
      }
      case 'tv_value_change': {
        let newSelectedList = state.selectedList.map(({ option, isSelected }) => { return { option: option, isSelected: false } });
        return {
          ...state,
          tvCount: action.value,
          selectedList: newSelectedList,
        }
      }
      default: {
        return state;
      }
    }
  },

  initialStateSingleMulti: (selectOptions) => {
    return {
      selectedList: selectOptions.map((option) => { return { option: option, isSelected: false } }),
      tvCount: '',
    }
  },

  initialState: {
    currentStep: 1,
    step1Results: null,
    step2Results: null,
    step3Results: null,
    step4Results: null,
    step5Results: null,
    disableButton: true,
    totalTvSteps: 6,
    totalInternetSteps: 4,
    numberOfSteps: 6,
    progressPercentage: 1,

  },
  reducer: (state, action) => {
    switch (action.type) {
      case 'step_result':
        return {
          ...state,
          [action.step_field]: action.step_result,
        }

      case 'next_step': {
        let tempPercentage = ((state.currentStep + 1) / (state.step1Results === 'ТВ + Интернет' ? state.totalTvSteps : state.totalInternetSteps)) * 100;
        tempPercentage = Math.round(tempPercentage);
        return {
          ...state,
          currentStep: state.currentStep + 1,
          disableButton: true,
          progressPercentage: tempPercentage,
          numberOfSteps: state.step1Results === 'ТВ + Интернет' ? state.totalTvSteps : state.totalInternetSteps,
        };
      }
      case 'prev_step': {
        let tempPercentage = ((state.currentStep - 1) / (state.step1Results === 'ТВ + Интернет' ? state.totalTvSteps : state.totalInternetSteps)) * 100;
        tempPercentage = Math.round(tempPercentage);
        return {
          ...state,
          currentStep: state.currentStep - 1,
          disableButton: true,
          progressPercentage: tempPercentage,
          numberOfSteps: state.step1Results === 'ТВ + Интернет' ? state.totalTvSteps : state.totalInternetSteps,
        };
      }
    }
  },

  parseInternetPackage: (s2, s3) => {
    return getInternetSpeed(s2, s3);
  },

  parseTvPackage: (s2, s3, s4, s5) => {
    console.log(s2);
    console.log(s3);
    console.log(s4);
    console.log(s5);

    // const numberOfTvs = getTvPrice(extractNumber(getTheLastTrueFromTheList(s4)));

    return {
      // package: getTheLastTrueFromTheList(s5),
      // internetSpeed: getInternetSpeed(s2, s3),
      // numberOfTvs: numberOfTvs,
    };
  }

}

const getInternetSpeed = (s2, s3) => {
  return calculateInternetSpeed(getTheLastTrueFromTheList(s2), extractNumber(getTheLastTrueFromTheList(s3)));
}

const getTvPrice = (numberOfTvs) => {
  const tvNum = parseInt(numberOfTvs);
  switch (tvNum) {
    case 1:
      return 0;
    case 2:
    case 3:
      return (tvNum - 1) * 2.5;
    default:
      return (tvNum - 3) * 5 + 5;
  }
}

const getTheLastTrueFromTheList = (list) => {
  for (var i = list.length - 1; i >= 0; i--) {
    if (list[i].isSelected) {
      return list[i].option;
    }
  }
  return null;
}

const extractNumber = (string) => {
  return string.trim().split(' ')[0];
}

const calculateInternetSpeed = (internetSpeed, numberOfDevices) => {
  const matrix = constants.step2SelectOptions;

  console.log(`internetSpeed: ${internetSpeed}`);
  console.log(`matrix: ${matrix[0]}`);
  switch (internetSpeed) {
    case matrix[0]: {
      const possibleSpeeds = ['85 Mbit/s', '85 Mbit/s', '125 Mbit/s', '125 Mbit/s'];
      return possibleSpeeds[numberOfDevices - 1];
    }
    case matrix[1]: {
      const possibleSpeeds = ['85 Mbit/s', '125 Mbit/s', '125 Mbit/s', '175 Mbit/s'];
      return possibleSpeeds[numberOfDevices - 1];
    }
    case matrix[2]: {
      const possibleSpeeds = ['175 Mbit/s', '225 Mbit/s', '225 Mbit/s', '300 Mbit/s'];
      return possibleSpeeds[numberOfDevices - 1];
    }
    case matrix[3]: {
      const possibleSpeeds = ['175 Mbit/s', '225 Mbit/s', '300 Mbit/s', '300 Mbit/s'];
      return possibleSpeeds[numberOfDevices - 1];
    }
  }
}