import React, { useReducer } from 'react';
import styled from 'styled-components';
import { useStaticQuery, graphql } from 'gatsby';
import { Line } from 'rc-progress';
import Step1 from './steps/step1';
import SingleMultiSelect from './steps/single-multi';
import constants from '../../constants/configurator';
import util from './util';

import logoImg from '../../images/nav/logo.svg';
import moneyGraphics from '../../images/configurator/money-graphics.svg';

const BackgroundWrapper = styled.div`
  background-image: url(${props => props.bg});
  background-position: top;
  background-repeat: no-repeat;
  background-size: contain;
  background: #151F41;
`;

const Configurator = () => {
  const data = useStaticQuery(graphql`
    query ConfiguratorQuery {
      leftSideBg: file(relativePath: {eq: "configurator/left-side-bg.png"}) {
          childImageSharp {
            fluid(maxHeight: 1000) {
              src
            }
          }
        }
    }
  `);

  const [state, dispatch] = useReducer(util.reducer, util.initialState);
  const { currentStep, step1Results, step2Results, step3Results, step4Results, step5Results, progressPercentage, numberOfSteps } = state;

  const onButtonNextClick = () => {
    // TODO: delete
    // console.log('step1Results');
    // console.log(step1Results);

    // console.log('step2Results');
    // console.log(step2Results);

    // console.log('step3Results');
    // console.log(step3Results);

    // console.log('step4Results');
    // console.log(step4Results);

    // console.log('step5Results');
    // console.log(step5Results);

    switch (currentStep) {
      case 1: {
        if (step1Results === null) {
          alert('Изберете поне едно от полетата');
          return;
        }
        break;
      }
      case 2: {
        if (step2Results === null) {
          alert('Изберете поне едно от полетата');
          return;
        }
        break;
      }
      case 3: {
        if (step3Results === null) {
          alert('Изберете поне едно от полетата');
          return;
        }
        if (step1Results === 'Само интернет') {
          console.log(util.parseInternetPackage(step2Results, step3Results));
          alert('Покажи пакети тук за интернет');
          return;
        }
        break;
      }
      case 4: {
        if (step4Results === null) {
          alert('Изберете поне едно от полетата');
          return;
        }
        break;
      }
      case 5: {
        if (step5Results === null) {
          alert('Изберете поне едно от полетата');
          return;
        }
        alert('Пакети ТВ + Нет');
        console.log(util.parseTvPackage(step2Results, step3Results, step4Results, step4Results))
        break;
      }
    }
    dispatch({ type: 'next_step' });
  }

  const StepComponentList = [
    <Step1
      key={'step1'}
      dispatch={dispatch}
    />,
    <SingleMultiSelect
      key={'step2'}
      dispatch={dispatch}
      dispatchHandle={'step2Results'}
      isSingle={false}
      selectOptions={constants.step2SelectOptions}
      title={constants.step2Title}
    />,
    <SingleMultiSelect
      key={'step3'}
      dispatch={dispatch}
      dispatchHandle={'step3Results'}
      isSingle={true}
      title={constants.step3Title}
      selectOptions={constants.step3SelectOptions}
    />,
    <SingleMultiSelect
      key={'step4'}
      dispatch={dispatch}
      dispatchHandle={'step4Results'}
      isSingle={true}
      title={constants.step4Title}
      selectOptions={constants.step4SelectOptions}
      hasOptionalInput={true}
    />,
    <SingleMultiSelect
      key={'step5'}
      dispatch={dispatch}
      dispatchHandle={'step5Results'}
      isSingle={true}
      title={constants.step5Title}
      selectOptions={constants.step5SelectOptions}
    />,
  ]
  return (
    <>
      <div className='d-flex justify-content-center' style={{paddingTop: '7rem'}}>
        <div className='col-12 col-xl-9' style={{ minHeight: 920 }}>
          <div className='row'>
            <BackgroundWrapper
              bg={data.leftSideBg.childImageSharp.fluid.src}
              className='col-5 d-flex justify-content-center'>
              <div className='text-center py-5'>
                <div className='row'>
                  <div className='col-10 d-flex justify-content-end'>
                    <img src={logoImg} className='img-fluid config-logo' alt="NetSurf лого" />
                  </div>
                  <div className='col-2 p-0 d-flex justify-content-start align-items-center'>
                    <button className='btn btn-primary dark-light-blue'>Асистент</button>
                  </div>
                </div>
                <p className='text-white mt-4 px-5'>Моля отговорете на въпросите за да създадете най-добрия пакет за вас</p>
                <img src={moneyGraphics} className='img-fluid' alt="Графика" />
                <div className='d-flex justify-content-between'>
                  <h3 className='text-white'>Прогрес</h3>
                  <h3 className='text-white'><span className='text-green'>{progressPercentage}%</span></h3>
                </div>
                <Line
                  percent={progressPercentage}
                  strokeColor="#60feb2"
                  trailColor="#FFFFFF"
                  strokeWidth={2}
                  trailWidth={2}
                />
              </div>
            </BackgroundWrapper>
            <div className='col-7 p-4 d-flex justify-content-end'>
              <div className='col-11 py-5'>
                <h3>Въпрос <span className='text-green'>{currentStep}</span> от {numberOfSteps}</h3>

                {/* < Container which renders different steps > */}
                {StepComponentList[currentStep - 1]}
                {/* </ Container which renders different steps >*/}

                <button
                  className={'btn btn-primary green my-5 mr-4'}
                  onClick={onButtonNextClick}>Следваща стъпка</button>
                {currentStep !== 1 && (
                  <button
                    className='btn btn-secondary my-5'
                    onClick={() => dispatch({ type: 'prev_step' })}>Назад</button>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export const StepTitle = ({ title }) => {
  return (
    <>
      <h2 className='my-2'>{title}</h2>
      <hr className='mb-5' />
    </>
  );
}

export default Configurator;